* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 2rem;
  height: 100vh;
  background: url("../images/logo-adalab.png") center center no-repeat #f3f4f5;
  font-family: "Roboto", sans-serif;
}

.page__header {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 3rem;
}

.main__list {
  padding-left: 2rem;
}

.link__adalab:hover {
  background-color: #099d8d;
  color: #fff;
}

.link__github:hover {
  background-color: #CCC;
  color: #000;
}

.page__footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  padding: 1rem;
  color: #999;
}
